<template>
  <div class="home">
    <section class="hero">
      <div class="hero-body">
        <div class="container">
          <img class="is-hidden-touch" alt="ACYS Camp Poster" src="@/assets/img/camp_banner.jpg" />
          <img class="is-hidden-desktop" alt="ACYS Camp Poster" src="@/assets/img/camp.jpg" />
        </div>
      </div>
    </section>
    <section class="section">
      <h1 class="title is-uppercase">News</h1>
      <h2 class="subtitle">ACYS Ipon</h2>
      <div class="container">
        <article class="media" v-for="member in sortedMembers" :key="member.id">
          <figure class="media-left">
            <p class="image is-64x64">
              <img class="is-rounded" :src="getImageUrl(member)" />
            </p>
          </figure>
          <div class="media-content">
            <div class="content">
              <p>
                <strong>{{ member.name }}</strong>
                <br />
                <progress
                  class="progress"
                  :class="getColor(member.progress)"
                  :value="member.progress"
                  :max="maxProgress"
                >{{ ((member.progress / maxProgress) * 100).toFixed().concat("%") }}</progress>
              </p>
            </div>
            <nav class="level is-mobile">
              <div class="level-left">
                <a class="level-item">
                  <span class="icon is-small">
                    <font-awesome-icon icon="share"></font-awesome-icon>
                  </span>
                </a>
                <a class="level-item">
                  <span class="icon is-small">
                    <font-awesome-icon icon="heart"></font-awesome-icon>
                  </span>
                </a>
                <a class="level-item">
                  <span class="icon is-small">
                    <font-awesome-icon icon="envelope"></font-awesome-icon>
                  </span>
                </a>
              </div>
            </nav>
          </div>
          <div class="media-right">
            <span
              class="is-small"
            >{{ ((member.progress / maxProgress) * 100).toFixed().concat("%") }}</span>
          </div>
        </article>
      </div>
    </section>
  </div>
</template>

<script>
const data = function() {
  return {
    maxProgress: 20,
    members: [
      {
        id: 1,
        name: "Migelle Jose Barlis",
        gender: "male",
        progress: 3
      },
      {
        id: 2,
        name: "Camille Joyce Gorospe",
        gender: "female",
        progress: 12
      },
      {
        id: 3,
        name: "Danica Love Suterio",
        gender: "female",
        progress: 8
      },
      {
        id: 4,
        name: "Jorina Gonzales",
        gender: "female",
        progress: 17
      }
    ]
  };
};

const computed = {
  sortedMembers() {
    return this.members.sort((a, b) => {
      return b.progress - a.progress;
    });
  }
};

const methods = {
  getColor(progress) {
    let percentage = parseInt(((progress / this.maxProgress) * 100).toFixed());
    if (0 <= percentage && percentage <= 25) {
      return "is-danger";
    } else if (25 < percentage && percentage <= 50) {
      return "is-warning";
    } else if (50 < percentage && percentage <= 75) {
      return "is-info";
    } else if (75 < percentage) {
      return "is-success";
    } else {
      return "is-black";
    }
  },

  getImageUrl({ id, gender }) {
    try {
      return require("../assets/img/" + id + "_profile.jpg");
    } catch (error) {
      return require("../assets/img/" + gender + ".svg");
    }
  }
};

export default {
  data,
  computed,
  methods
};
</script>
